import { Component, OnInit, OnDestroy } from '@angular/core';

import { aboutEnterAnimation } from '../Animations/about.enter.animation';

import { AppService } from '../app.service';
import { debounceTime } from 'rxjs/operators';
import { Subscription } from 'rxjs';

@Component({
  selector: 'ukt-contact',
  templateUrl: './contact.component.html',
  styleUrls: ['./contact.component.scss'],
  animations: [aboutEnterAnimation]
})
export class ContactComponent implements OnInit, OnDestroy {
  marker = {
    lat: 40.095699,
    lng: 29.513072
  };

  origin;
  navigationView = false;

  lat = this.marker.lat;
  lng = this.isMobile ? this.marker.lng : 29.511557;
  zoom = 17;

  loaded = false;
  ready = false;

  informed = false;

  sub: Subscription;

  constructor(private _appService: AppService) {}

  ngOnInit() {
    this._appService.updateMeta({
      title: 'Ä°letiÅim',
      description: 'EtkileÅim, ulaÅÄ±labilirlik ile baÅlar.',
      image: '',
      route: 'iletisim'
    });

    this.sub = this._appService.ui
      .pipe(debounceTime(100))
      .subscribe(state => (this.ready = !state.isBusy));
  }

  ngOnDestroy() {
    this.sub.unsubscribe();
  }

  requestNavigation() {
    if (this._appService.isBrowser) {
      if (navigator.geolocation) {
        navigator.geolocation.getCurrentPosition(
          pos => {
            this.origin = {
              lat: pos.coords.latitude,
              lng: pos.coords.longitude
            };

            this.navigationView = true;
          },
          err => {
            console.log('Falled into error: ', err.message);
            this.requestNavigation();
          },
          { maximumAge: 60000, timeout: 5000, enableHighAccuracy: true }
        );
      }
    }
  }

  get isReady() {
    return this.loaded && this.ready;
  }

  get isMobile() {
    return this._appService.isMobile;
  }

  get animateOrWhat() {
    return this.isMobile ? 'disabled' : this.isReady;
  }

  toggleInfo() {
    this.informed = !this.informed;
  }
}
